<style lang="scss">
  .eventItem {
    &__title {
      color: rgba(0, 0, 0, .87);
      font-weight: 600;
      margin-bottom: 1.25rem;
    }
    &__card {
      @include media("<=tablet") {
        width: 100%;
        margin: .5rem 0;
      }
      height: 100%;
      box-shadow: 1px 1px 2px rgba(0, 0, 0, .16), 0 -.5px 1px rgba(0, 0, 0, .16);

      background-color: #fefefe;

      display: flex;
      flex-direction: column;
      margin: 0 .5rem;
      flex: 1;
      justify-content: space-between;
    }

    &__speakers {
      padding: 1rem;
      flex-wrap: wrap;

      background-color: lighten($theme-blue, 5%);
    }
    &__speaker {
      margin-bottom: .2rem;
      &__name {
        font-size: 1rem;
      }
      &__meta {
        font-size: .8rem;
      }
      &__name, &__meta {
        color: $theme-yellow;
      }

      &__info:not(:last-child) {
        &::after {
          content: ' /';
        }
      }
    }
    &__content {
      overflow-y: hidden;
    }
  }

  .topicWrapper {
    display: block;
    flex: 1;
  }
</style>

<template>
  <topic-wrapper
    :event="event"
    class="eventItem__link"
  >
    <div class="card is-flex eventItem__card">
      <div class="card-content">
        <div class="media">
          <div class="media-content eventItem__content">
            <p class="eventItem__title">
              {{ event.display }}
            </p>
            <p
              v-if="event.language || event.level || event.venue.name"
              class="subtitle is-6"
            >

              <span
                v-if="event.venue.name"
                class="eventItem__speaker__info"
              >
                {{ event.venue.name }}
              </span>
              <span
                v-if="event.language"
                class="eventItem__speaker__info"
              >
                {{ event.language }}
              </span>
              <span
                v-if="event.speakers && event.speakers.length"
                class="eventItem__speaker__info"
              >
                {{ speakerName }}
              </span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </topic-wrapper>
</template>

<script>
export default {
  name: 'EventItem',
  components: {
    TopicWrapper: () => import('./TopicWrapper'),
  },
  props: {
    event: {
      type: Object,
      required: true,
    },
  },
  computed: {
    speakerName() {
      return this.event.speakers.map(s => s.name).join(' & ');
    },
  },
};
</script>
